import React from "react";
import { Menu, Form, Container, Checkbox, Message } from "semantic-ui-react";
import { auth } from "../../utils/firebase";
import { useNavigate } from "react-router-dom";  //  useNavigate  useHistory
import { createUserWithEmailAndPassword, signInWithEmailAndPassword } from 'firebase/auth';

function SigninZh() {
  const [activeItem, setActiveItem] = React.useState('signin');
  const [email, setEmail] = React.useState('');
  const [password, setPassword] = React.useState('');
  const [confirmPassword, setConfirmPassword] = React.useState(''); 
  const [showPassword, setShowPassword] = React.useState(false);
  const navigate = useNavigate();  //  useNavigate
  const [errorMessage, setErrorMessage] = React.useState('');
  const [isLoading, setIsLoading] = React.useState(false);
  const isMounted = React.useRef(true);

  React.useEffect(() => {
      isMounted.current = true;
      return () => {
        isMounted.current = false;
      };
  }, []);


  function onSubmit() {
    setIsLoading(true);
    if (activeItem === 'register') {
      if (password !== confirmPassword) { // 
        setErrorMessage('');
        setIsLoading(false);
        return;
      }

      createUserWithEmailAndPassword(auth, email, password)

      .then(() => auth.signOut()) // 

      .then(() => { 
        if (isMounted.current) {
          setIsLoading(false);
          navigate('/zh/registersuccess');
        }
      })
      .catch((error) => {
        console.error("Error:", error);
        if (isMounted.current) {
          switch (error.code) {
            case 'auth/email-already-in-use':
              setErrorMessage('');
              break;
            case 'auth/invalid-email':
              setErrorMessage('');
              break;
            case 'auth/weak-password':
              setErrorMessage('');
              break;
            default:
              setErrorMessage('');
          }
          setIsLoading(false);
        }
      });

    } else if (activeItem === 'signin') {

      signInWithEmailAndPassword(auth, email, password)
      .then(() => {
        if (isMounted.current) {
          navigate('/zh/posts');  //  navigate  history.push
          setIsLoading(false);
        }
      })
      .catch((error) => {
        console.error("Error:", error);
        if (isMounted.current) {
          switch (error.code) {
            case 'auth/invalid-email':
              setErrorMessage('');
              break;
            case 'auth/invalid-credential':
              setErrorMessage('');
              break;
            default:
              setErrorMessage('');
          }
          setIsLoading(false);
        }
      });

    }
  }

  return (
    <Container>
      <Menu widths='2' color="blue">
        <Menu.Item active={activeItem === 'signin'} onClick={() => {
          setErrorMessage('')
          setActiveItem('signin')}}>
          
        </Menu.Item>
          
        <Menu.Item active={activeItem === 'register'} onClick={() => {
          setErrorMessage('')
          setActiveItem('register')}}>
          
        </Menu.Item>
      </Menu>

      <Form onSubmit={onSubmit}>
        <Form.Input label='' value={email} onChange={(e) => setEmail(e.target.value)} placeholder='' />
        <Form.Input label='' type={showPassword ? 'text' : 'password'} value={password} onChange={(e) => setPassword(e.target.value)} placeholder='' />
        {activeItem === 'register' && (
          <Form.Input 
            label='' 
            type={showPassword ? 'text' : 'password'} 
            value={confirmPassword} 
            onChange={(e) => setConfirmPassword(e.target.value)} 
            placeholder='' 
          />
        )}
        <Form.Field>
          <Checkbox label='' checked={showPassword} onChange={() => setShowPassword(!showPassword)} />
        </Form.Field>
        {errorMessage && <Message negative>{errorMessage}</Message>}
        <Form.Button loading={isLoading}>
          {activeItem === 'signin' && ''}
          {activeItem === 'register' && ''}
        </Form.Button>
      </Form>
    </Container>
  );
}

export default SigninZh;
