import React from 'react';
import { Grid, Item, Container, Header,Button, Segment, Modal,Input } from 'semantic-ui-react';
import { Link } from 'react-router-dom';
import { getAuth, updateProfile, EmailAuthProvider, reauthenticateWithCredential, updatePassword } from "firebase/auth";

function MyName({user}){
  const [isModalOpen, setIsModalOpen] = React.useState(false);
  const [displayName, setDisplayName] = React.useState('');
  const [isLoading, setIsLoading] = React.useState(false);

  const handleOpenModal = () => {
    setIsModalOpen(true);
    setDisplayName('');  // 
  }

  function onSubmit() {
    setIsLoading(true);

    updateProfile(user, {
      displayName: displayName,
    })
    .then(()=>{
    setDisplayName('');
    setIsModalOpen(false);
    setIsLoading(false);
    })
    .catch((error) => {
      console.error(":", error);
      setIsLoading(false);
    });
  }

  return(
    <>
      <Header size='small'>
        
        <Button floated='right' onClick={handleOpenModal}></Button>
      </Header>
      <Segment vertical >{user?.displayName || ""}</Segment>
      <Modal size='mini' open={isModalOpen}>
        <Modal.Header></Modal.Header>
        <Modal.Content>
          <Input
          fluid
          placeholder='' 
          value={displayName} 
          onChange={(e) => setDisplayName(e.target.value)}
          />
        </Modal.Content>
        <Modal.Actions>
          <Button onClick={()=> setIsModalOpen(false)}></Button>
          <Button onClick={onSubmit} loading={isLoading} ></Button>
        </Modal.Actions>
      </Modal>
    </>
  )
}

function MyPassword({user}){
  const [isModalOpen, setIsModalOpen] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(false);
  const [oldPassword, setOldPassword] = React.useState('');
  const [newPassword, setNewPassword] = React.useState('');
  const [confirmPassword, setConfirmPassword] = React.useState('');
  const [isPasswordVisible, setIsPasswordVisible] = React.useState(false);

  const handleOpenModal = () => {
    setIsModalOpen(true);
    setOldPassword('');  // 
    setNewPassword('');   // 
    setConfirmPassword('');  // 
    setIsPasswordVisible(false); // 
  }

  function onSubmit() {
    if (newPassword !== confirmPassword) {
      alert("");
      return;
    }

    setIsLoading(true);
    const auth = getAuth();
    const credential = EmailAuthProvider.credential(user.email, oldPassword); // 

    reauthenticateWithCredential(auth.currentUser, credential) //  auth.currentUser 
      .then(() => {
        updatePassword(auth.currentUser, newPassword) //  auth.currentUser 
          .then(() => {
            setIsModalOpen(false);
            setOldPassword('');
            setNewPassword('');
            setConfirmPassword('');
            setIsLoading(false);
          })
          .catch((error) => {
            console.error(":", error);
            setIsLoading(false);
          });
      })
      .catch((error) => {
        console.error(":", error);
        setIsLoading(false);
      });
  }

  return (
    <>
      <Header size='small'>
        
        <Button floated='right' onClick={handleOpenModal}>
          
          </Button>
      </Header>
      <Segment vertical ></Segment>
      <Modal size='mini' open={isModalOpen}>
        <Modal.Header></Modal.Header>
        <Modal.Content>
          <Header></Header>
          <Input
          fluid
          placeholder='' 
          value={oldPassword} 
          onChange={(e) => setOldPassword(e.target.value)}
          type={isPasswordVisible ? 'text' : 'password'}
          />
          <Header></Header>
          <Input
          fluid
          placeholder='' 
          value={newPassword} 
          onChange={(e) => setNewPassword(e.target.value)}
          type={isPasswordVisible ? 'text' : 'password'}
          />
          <Header></Header>
          <Input
          fluid
          placeholder=''
          value={confirmPassword}
          onChange={(e) => setConfirmPassword(e.target.value)}
          type={isPasswordVisible ? 'text' : 'password'}
          />

          <div style={{ marginTop: '10px' }}>
            <Input
              type="checkbox"
              label=""
              onChange={() => setIsPasswordVisible(!isPasswordVisible)}  // 
            />
          </div>


        </Modal.Content>
        <Modal.Actions>
          <Button onClick={()=> setIsModalOpen(false)}></Button>
          <Button onClick={onSubmit} loading={isLoading} ></Button>
        </Modal.Actions>
      </Modal>
    </>
  )
}

function MySettingsZh ({user}){

  return (
    <Container>

    <Grid>
  <Grid.Row>
    <Grid.Column width={2}></Grid.Column>

    <Grid.Column width={11}>
      <>
      <Header></Header>
      <MyName user={user} />
      <MyPassword user={user} />
      <Button color='pink' as={Link} to='/zh/posts' style={{ marginTop: '5px' }} ></Button>
      </>

    </Grid.Column>

    <Grid.Column width={2}></Grid.Column>

  </Grid.Row>
</Grid>

    </Container>

  )
}

export default MySettingsZh;