import React from "react";
import { Container, Header, Form, Modal, Button } from "semantic-ui-react";
import { db } from "../../utils/firebase";
import { collection, getDocs, addDoc, serverTimestamp } from "firebase/firestore";
import { getAuth } from "firebase/auth";
import { useNavigate } from 'react-router-dom';

function NewArtistJp() {
  const navigate = useNavigate();
  const [title, setTitle] = React.useState('');
  const [isLoading, setIsLoading] = React.useState(false);
  const [open, setOpen] = React.useState(false);  //  Modal 
  
  async function onSubmit() {
    if (!title.trim()) {
      setOpen(true);  //  Modal 
      return;
    }

    setIsLoading(true);
    try {
      const auth = getAuth(); // 
      const user = auth.currentUser;
      
      if (!user) {
        console.error("No authenticated user");
        return;
      }

      //  Firebase
      await addDoc(collection(db, "posts"), {
        title,
        createdAt: serverTimestamp(),
        author: {
          displayName: user.displayName || '',
          uid: user.uid,
          email: user.email,
        }
      });

      // 
      setTitle('');  // 
      setIsLoading(false);
      navigate('/jp/posts');  // 
    } catch (error) {
      console.error("Error adding document:", error);
      setIsLoading(false);  // 
    }
  }

  return (
    <Container>
      <Header></Header>
      <Form onSubmit={onSubmit}>
        <Form.Input
          placeholder=""
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
        
        <div style={{ display: 'flex', gap: '10px', marginTop: 15 }}>
          <Form.Button
            color="teal"
            loading={isLoading}
            style={{
              marginVertical: 15, width: 50,
              fontSize: '10px',
            }}
          >
            
          </Form.Button>

          <Form.Button
            onClick={() => navigate('/jp/posts')}
            style={{
              marginVertical: 15, width: 50,
              fontSize: '10px',
            }}
          >
            
          </Form.Button>
        </div>
      </Form>

      {/* Modal  */}
      <Modal open={open} onClose={() => setOpen(false)}>
        <Modal.Header>Warning</Modal.Header>
        <Modal.Content>
          <p></p>
        </Modal.Content>
        <Modal.Actions>
          <Button onClick={() => setOpen(false)}></Button>
        </Modal.Actions>
      </Modal>
    </Container>
  );
}

export default NewArtistJp;
